<template>
    <div id="app">
        <div v-if="files === undefined" class="main row h-100">
            <div class="col-sm-12 my-auto">
                <b-form-file multiple class="dropfiles" @input="chooseFiles" v-model="chosenfiles" placeholder="Choose input files or drop them here..." drop-placeholder="Drop files here...">
                </b-form-file>
                <!-- <div class="mt&#45;3 or&#45;text">or</div> -->
                <!-- <b&#45;form&#45;file directory no&#45;traverse class="dropfiles" @input="chooseFiles" v&#45;model="chosenfiles" placeholder="Choose a directory or drop it here..." drop&#45;placeholder="Drop directory here..."> -->
                <!-- </b&#45;form&#45;file> -->
            </div>
        </div>
        <Main v-else v-bind:files="files"/>
    </div>
</template>

<script>
import Main from './components/Main.vue'

function readFileAsync(file) {
    return new Promise((resolve, reject) => {
        let reader = new FileReader();

        reader.onload = () => {
            resolve(reader.result);
        };

        reader.onerror = reject;
        reader.readAsDataURL(file);
    });
}

export default {
    name: 'App',
    components: {
        Main
    },
    data: function() {
        return {
            chosenfiles: undefined,
            files: undefined
        };
    },
    methods: {
        async chooseFiles(ev) {
            this.$data.chosenfiles.sort((a,b) => { if (a.name < b.name) return -1; if (b.name < a.name) return 1; return 0; });
            this.$data.files = [];
            for (var k in this.$data.chosenfiles) {
                let file = this.$data.chosenfiles[k];
                let src = await readFileAsync(file);

                this.$data.files.push({
                    file:file,
                    sortkey:k,
                    src:src,
                    state: null
                });
            }
        }
    }
}
</script>

