<template>
    <div class="main row h-100">
        <div class="col-sm-2 h-100 left-bar">
            <b-container fluid class="h-100 w-100 bg-dark thumbcontainer overflow-auto">
                <b-row v-for="file in files" v-bind:class="{ 'thumb-row': true, 'active-thumb': file.sortkey == selected, 'positive-thumb': (file.state === true), 'negative-thumb': (file.state === false) }" @click="chooseThumb(file.sortkey)" v-bind:key="file.sortkey">
                    <b-col>
                        <b-img v-if="file.src" thumbnail fluid alt="[invalid file]" class="thumb" v-bind:src="file.src" />
                    </b-col>
                </b-row>
            </b-container>
        </div>
        <div class="col-sm-10 h-100 editarea overflow-auto p-0">
            <nav class="navbar sticky-top navbar-expand-lg navbar-dark bg-dark">
                <span class="sblabel">Saturation:</span>
                <b-form-input type="range" id="sb-sat" class="sb" v-model="saturation" min="1" max="200"></b-form-input>
                <span class="sblabel">Contrast:</span>
                <b-form-input type="range" id="sb-contrast" class="sb" v-model="contrast" min="1" max="200"></b-form-input>
                <span class="sblabel">Always show full image:</span>
                <b-form-checkbox id="checkbox-1" v-model="alwaysFull" name="alwaysFull"  />
            </nav>
            <b-img fluid alt="alt name" v-bind:class="{'h-100': alwaysFull, 'editimg': true}" v-if="files.length > selected" v-bind:src="files[selected].src" v-bind:style="{ filter: imgFilter }" />
            <div class="buttonarea">
                <b-button variant="success" class="btn" @click="setState(true)"><b-icon icon="check" aria-hidden="true" /></b-button>
                <b-button variant="danger" class="btn" @click="setState(false)"><b-icon icon="x" aria-hidden="true" /></b-button>
                <b-button class="btn" @click="setState(null)"><b-icon icon="question" aria-hidden="true" /></b-button>
                <b-button variant="primary" class="btn" @click="saveStates()"><b-icon icon="download" aria-hidden="true" /></b-button>
            </div>
        </div>
    </div>
    <!--
    <div class="col-sm-12 my-auto">
    </div>
    -->
</template>

<script>
export default {
    name: 'Main',
    props: ['files'],
    data: function() {
        return {
            selected: 0,
            alwaysFull: false,
            saturation: 100,
            contrast: 100
        };
    },
    mounted: function() {
        // for (var f in this.$props.files) {
            // console.log(this.$props.files[f].src);
        // }
    },
    computed: {
        imgFilter: function() {
            return "saturate(" + (this.$data.saturation / 100.0) + ") contrast(" + (this.$data.contrast / 100.0) + ")";
        }
    },
    methods: {
        chooseThumb(k) {
            this.$data.selected = k;
        },
        setState(state) {
            this.$props.files[this.$data.selected].state = state;
            let next = parseInt(this.$data.selected) + 1
            if (next >= this.$props.files.length) {
                next = 0;
            }
            this.$data.selected = next;
        },
        saveStates() {
            // let content = "data:text/csv;charset=utf-8,";
            let content = "";
            for (let k in this.$props.files) {
                let name = this.$props.files[k].file.name;
                let state = this.$props.files[k].state;
                let state_str = "";
                if (state === true) state_str = "TRUE";
                if (state === false) state_str = "FALSE";
                content += ["\""+name+"\"",state_str].join(';') + "\r\n";
            }
            let blob = new Blob([content], {type: 'text/csv'});
            const link = document.createElement('a')
            link.href = URL.createObjectURL(blob)
            link.download = "output.csv"
            link.click()
            URL.revokeObjectURL(link.href)
            // var encoded = encodeURI(content);
            // window.open(encoded);
        },
    }
}
</script>

